import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { AppSettings } from '../../../core/app-settings';

@Injectable({
  providedIn: 'root'
})
export class InventoryService {
  baseUrl = AppSettings.API_ENDPOINT;
  constructor(private http: HttpClient) { }
  // Method: declaring api to fetch product-listing data
  getProductList(queryStr: string) {
    if (!!queryStr) {
      queryStr = '?' + queryStr;
    } else {
      queryStr = '';
    }
    return this.http.get<any>(this.baseUrl + 'admin/web/inventory/api/get/allProducts' + queryStr);
  }
  // Method: declaring api to fetch diagnostic-listing data
  getDiagnosticsList(queryStr: string) {
    if (!!queryStr) {
      queryStr = '?' + queryStr;
    } else {
      queryStr = '';
    }
    return this.http.get<any>(this.baseUrl + 'admin/web/inventory/api/get/allDiagnosticProducts' + queryStr);
  }
  // Method: declaring api to fetch diagnostic-listing data
  getMedicineList(queryStr: string) {
    if (!!queryStr) {
      queryStr = '?' + queryStr;
    } else {
      queryStr = '';
    }
    return this.http.get<any>(this.baseUrl + 'admin/web/inventory/api/get/allMedicines' + queryStr);
  }
  // Method: getting Type from BE
  gettypeDropdown(queryStr: string) {
    if (!!queryStr) {
      queryStr = '?' + queryStr;
    } else {
      queryStr = '';
    }
    return this.http.get<any>(this.baseUrl + 'api/user/eproducts/get/getEnumByName' + queryStr);
  }
  // Method: getting Array from BE
  getENUMData(queryStr) {
    return this.http.get<any>(this.baseUrl + 'api/user/eproducts/get/getEnumByName' + queryStr);
  }
  // Method: Save Product Data from BE
  saveProductData(data: any) {
    return this.http.post<any>(this.baseUrl + 'admin/web/inventory/api/create/addProduct', data);
  }
  // Method: Edit Product Data from BE
  editProductData(data: any) {
    return this.http.post<any>(this.baseUrl + 'admin/web/inventory/api/update/product', data);
  }
  // Method: declaring api to view products details
  getProductDetailById(queryStr: string) {
    if (!!queryStr) {
      queryStr = '?' + queryStr;
    } else {
      queryStr = '';
    }
    return this.http.get<any>(this.baseUrl + 'admin/web/inventory/api/get/getProductDetails' + queryStr);
  }
  // Method: declaring api to view diagnostic products details
  getDiagnosticDetailById(queryStr: string) {
    if (!!queryStr) {
      queryStr = '?' + queryStr;
    } else {
      queryStr = '';
    }
    return this.http.get<any>(this.baseUrl + 'admin/web/inventory/api/get/getDiagnosticProductDetails' + queryStr);
  }
  // Method: declaring api to view diagnostic products details
  getMedicineDetailById(queryStr: string) {
    if (!!queryStr) {
      queryStr = '?' + queryStr;
    } else {
      queryStr = '';
    }
    return this.http.get<any>(this.baseUrl + 'admin/web/inventory/api/get/getMedicineDetails' + queryStr);
  }
  // Method: declaring api to view item details
  getpincodedetails(queryStr: string) {
    if (!!queryStr) {
      queryStr = '?' + queryStr;
    } else {
      queryStr = '';
    }
    return this.http.get<any>(this.baseUrl + 'public/api/get/locationByPincode' + queryStr);
  }
  // Method: declaring api to delete product
  removeProduct(queryStr: string) {
    if (!!queryStr) {
      queryStr = '?' + queryStr;
    } else {
      queryStr = '';
    }
    return this.http.delete<any>(this.baseUrl + 'admin/web/inventory/api/delete/removeProductById' + queryStr);
  }
  // Method: declaring api to delete diagnostic product
  removeDiagnosticProduct(queryStr: string) {
    if (!!queryStr) {
      queryStr = '?' + queryStr;
    } else {
      queryStr = '';
    }
    return this.http.delete<any>(this.baseUrl + 'admin/web/inventory/api/delete/removeDiagnosticProductById' + queryStr);
  }
  // Method: declaring api to delete medicine
  removeMedicine(queryStr: string) {
    if (!!queryStr) {
      queryStr = '?' + queryStr;
    } else {
      queryStr = '';
    }
    return this.http.delete<any>(this.baseUrl + 'admin/web/inventory/api/delete/removeMedicine' + queryStr);
  }
  // End of above code
  // Method: declaring api to fetch items for category-listing
  getCategoryList(queryStr: string) {
    if (!!queryStr) {
      queryStr = '?' + queryStr;
    } else {
      queryStr = '';
    }
    return this.http.get<any>(this.baseUrl + 'category-listing/' + queryStr);
  }
  // End of above cod
  // Method: declaring api to fetch items for dropdown in categpry-add in category list
  getCategoryDropdownData() {
    return this.http.get<any>(this.baseUrl + 'category-create/');
  }
  // End of above code
  // Method: declaring api to send data to add items to category-listing
  saveCategoryData(data: any) {
    return this.http.post<any>(this.baseUrl + 'category-create/', data);
  }
  // End of above code
  // Method: declaring api to edit data to add items to category-listing
  editCategoryData(data: any) {
    return this.http.post<any>(this.baseUrl + 'category-edit/', data);
  }

  // Below Methpds are used for Temporary Use
  getVendorDetailById(queryStr: string) {
    if (!!queryStr) {
        queryStr = '?' + queryStr;
    } else {
        queryStr = '';
    }
    return this.http.get<any>(this.baseUrl + 'admin/api/vendor/getVendorDetails' + queryStr);
  }
  saveVendorData(data: any) {
    return this.http.post<any>(this.baseUrl + 'admin/web/inventory/api/create/addProduct', data);
  }
  editVendorData(data: any) {
    return this.http.post<any>(this.baseUrl + 'admin/web/inventory/api/update/product', data);
  }
}
